import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { Typography, Box } from '@mui/material';
import ProductTable from './tables/ProductTable';
import CustomerTable from './tables/CustomerTable';
import OrderTable from './tables/OrderTable';
import BreadCrumb from '../breadcrumbs/BreadCrumb';
import { useNavigate } from 'react-router-dom';

const ManagementDashboard = () => {
    const [activeTable, setActiveTable] = useState('products');
    const user = useSelector((state) => state.user);
    const navigate = useNavigate();

    //Chuyn hng v homepage nu khng c quyn admin
    useEffect(() => {
        if (!user.isLoggedIn || !user.userRole.includes('Admin')) {
            navigate('/');
        }
    }, [user, navigate, user.userRole]);

    //BreadCrumb
    const breadcrumbs = [
        {
            name: "Trang ch",
            url: "/"
        },
        {
            name: "Management Dashboard",
            url: "/admin"
        }
    ];

    return (
        <div>
            <BreadCrumb breadcrumbs={breadcrumbs} />
            {/* Tiu  */}
            <Typography variant="h3" align="center" style={{ margin: '20px 0', fontWeight: 'bold' }}>
                Dashboard Qun L
            </Typography>
            {/* Cc nt  chuyn i gia cc bng */}
            <Box display="flex" justifyContent="center" alignItems="center" marginBottom={2}>
                <button
                    variant="contained"
                    style={{
                        backgroundColor: '#0277bd',
                        color: 'white',
                        margin: '0 10px',
                        borderRadius: '20px',
                        padding: '10px 20px'
                    }}
                    onClick={() => setActiveTable('products')}
                >
                    Sn Phm</button>
                <button
                    variant="contained"
                    style={{
                        backgroundColor: '#0277bd',
                        color: 'white',
                        margin: '0 10px',
                        borderRadius: '20px',
                        padding: '10px 20px'
                    }}
                    onClick={() => setActiveTable('customers')}
                >
                    Khch Hng</button>
                <button
                    variant="contained"
                    style={{
                        backgroundColor: '#0277bd',
                        color: 'white',
                        margin: '0 10px',
                        borderRadius: '20px',
                        padding: '10px 20px'
                    }}
                    onClick={() => setActiveTable('orders')}
                >
                    n Hng</button>
            </Box>
            {/* Hin th bng da trn activeTable */}
            {activeTable === 'products' && <ProductTable />}
            {activeTable === 'customers' && <CustomerTable />}
            {activeTable === 'orders' && <OrderTable />}
        </div>
    );
};

export default ManagementDashboard;