import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Grid, Card, CardHeader, CardMedia, CardContent, Typography, Button } from '@mui/material';

const ProductItem = ({ name, imageUrl, description, category, buyPrice, promotionPrice, _id, amount, className }) => {
    const navigate = useNavigate();
    const navigateToProductDetails = () => {
        // Prevent default anchor action
        navigate(`/products/${_id}`);
    };
    const addToCart = () => {
        // Kim tra nu amount l 0 th khng thc hin thm vo gi hng
        if (amount === 0) {
            return;
        }

        const cart = JSON.parse(localStorage.getItem('cart') || '[]');

        // Kim tra nu nh c sn phm cng id  tn ti trong gi hng
        const existingProductIndex = cart.findIndex((product) => product.id === _id);

        if (existingProductIndex !== -1) {
            // Nu c sn phm , tng s lng
            cart[existingProductIndex].quantity += 1;
        } else {
            // Nu nh khng c sn phm , thm sn phm vo gi hng vi s lng = 1
            const productToAdd = {
                id: _id,
                name: name,
                price: buyPrice,
                quantity: 1
            };
            cart.push(productToAdd);
        }

        localStorage.setItem('cart', JSON.stringify(cart));
        window.dispatchEvent(new Event('cartUpdated'));
    };

    // Font Style
    const buttonStyle = {
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: 'larger',
        color: '#01723e',
        cursor: 'pointer'
    };

    // Custom styles cho Loi v Gi
    const textStyle = {
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: 'larger',
        color: '#01723e'
    };

    // Style gch gi c
    const originalPriceStyle = {
        textDecoration: 'line-through',
        marginRight: '10px'
    };

    return (
        <Grid item xs={12} sm={6} md={4} className={className}>
            <Card className="product-box bg-light mb-3" style={{ backgroundColor: '#01723e', display: 'flex', flexDirection: 'column', height: '100%' }}>
                <CardHeader
                    title={<Button onClick={navigateToProductDetails} style={buttonStyle}>{name}</Button>}
                    className="text-center"
                />
                <CardMedia
                    component="img"
                    image={imageUrl}
                    alt="product"
                    onClick={navigateToProductDetails}
                    style={{ cursor: 'pointer' }}
                />
                <CardContent>
                    <Typography variant="body2" className="card-text description">{description}</Typography>
                    <Typography style={textStyle}>
                        <b>Loi:</b> {category}
                    </Typography>
                    <Typography style={textStyle}>
                        <b>Gi:</b> <span style={originalPriceStyle}>${buyPrice}</span> ${promotionPrice}
                    </Typography>
                    <div className="add-cart-container text-center">
                        <Button variant="contained" color="secondary" onClick={navigateToProductDetails}>Chi tit</Button>
                        {amount === 0 ? (
                            <div style={{
                                backgroundColor: 'red',
                                color: 'white',
                                borderRadius: '10%',
                                padding: '11px 10px',
                                display: 'inline-block',
                                marginTop: '10px',
                                fontSize: '14px'
                            }}>
                                 Ht Hng
                            </div>
                        ) : (
                            <Button variant="contained" color="primary" onClick={addToCart}>Thm vo gi</Button>
                        )}
                    </div>
                </CardContent>
            </Card>
        </Grid>
    );
};

export default ProductItem;
