import * as React from 'react';
import SummarizeIcon from '@mui/icons-material/Summarize';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import AddReactionIcon from '@mui/icons-material/AddReaction';
import IconButton from '@mui/material/IconButton';
import Tooltip from '@mui/material/Tooltip';
import { Menu, MenuItem } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import Badge from '@mui/material/Badge';
import { useEffect } from 'react';

export default function IconNavBar() {
    const [cartItemCount, setCartItemCount] = React.useState(0);
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [isLoggedIn, setIsLoggedIn] = React.useState(false); // Trng thi ng nhp
    const navigate = useNavigate();

    // Retrieve the cart data from local storage
    const updateCartCount = () => {
        const cart = JSON.parse(localStorage.getItem('cart') || '[]');
        setCartItemCount(cart.length);
    };

    useEffect(() => {
        updateCartCount();
        // X l s kin cp nht s lng trn icon gi hng
        window.addEventListener('cartUpdated', updateCartCount);

        // Kim tra trng thi ng nhp t sessionStorage
        const loggedIn = sessionStorage.getItem('isLoggedIn') === 'true';
        setIsLoggedIn(loggedIn);

        // Xa s trn icon gi hng
        return () => {
            window.removeEventListener('cartUpdated', updateCartCount);
        };
    }, []);

    // Hm x l ng xut
    const handleLogout = () => {
        sessionStorage.removeItem('isLoggedIn'); // Xa trng thi ng nhp khi sessionStorage
        sessionStorage.removeItem('token');
        sessionStorage.removeItem('refreshToken');
        sessionStorage.removeItem('userId');
        sessionStorage.removeItem('userRole');
        sessionStorage.removeItem('customerInfo');
        setIsLoggedIn(false); // Cp nht trng thi ng nhp
        navigate("/");
        handleClose();
    };

    const handleMenu = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleSignUp = () => {
        // Logic  chuyn n trang Sign Up
        navigate("/signup");
        handleClose();
    };

    const handleLogin = () => {
        // Logic  chuyn n trang Login
        navigate("/login");
        handleClose();
    };

    const handleCart = () => {
        navigate("/cart");
    };

    const handleAdmin = () => {
        navigate("/admin");
    };

    //Ly UserId  hin th
    const username = sessionStorage.getItem('userId') || 'Ngi Dng';


    return (
        <div>
            <Tooltip title="Cng C Qun L">
                <IconButton
                    size="large"
                    edge="start"
                    color="black"
                    aria-label="menu"
                    sx={{ mr: 2 }}
                    onClick={handleAdmin}
                >
                    <SummarizeIcon />
                </IconButton>
            </Tooltip>
            <Tooltip title="Ti Khon">
                <IconButton
                    size="large"
                    edge="start"
                    color="black"
                    aria-label="menu"
                    sx={{ mr: 2 }}
                    onClick={handleMenu}
                >
                    {isLoggedIn ? <AddReactionIcon /> : <AccountCircleIcon />}
                </IconButton>
            </Tooltip>
            <Tooltip title="Gi Hng">
                <IconButton
                    size="large"
                    edge="start"
                    color="black"
                    aria-label="menu"
                    sx={{ mr: 2 }}
                    onClick={handleCart}
                >
                    <Badge badgeContent={cartItemCount} color="secondary">
                        <ShoppingCartIcon />
                    </Badge>
                </IconButton>
            </Tooltip>
            <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                }}
                open={Boolean(anchorEl)}
                onClose={handleClose}
            >
                <div>
                    {isLoggedIn ? (
                        <>
                            <MenuItem disabled>{` ng Nhp: ${username}`}</MenuItem>
                            <MenuItem onClick={handleLogout}>ng Xut</MenuItem>
                        </>
                    ) : (
                        <>
                            <MenuItem onClick={handleSignUp}>ng K</MenuItem>
                            <MenuItem onClick={handleLogin}>ng Nhp</MenuItem>
                        </>
                    )}
                </div>
            </Menu>
        </div>
    );
}