import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from 'react-redux';
import { Button, Modal, Table, TableBody, TableCell, TableHead, TableRow, TextField, Box, Typography, Paper, FormControl, InputLabel, Select, MenuItem, Grid, TableContainer } from '@mui/material';
import { fetchProvinces } from "../../../actions/actions";


const CustomerTable = () => {
    const dispatch = useDispatch();
    const [customers, setCustomers] = useState([]);
    const [filteredCustomers, setFilteredCustomers] = useState(customers);
    const [searchTerm, setSearchTerm] = useState('');
    const provinces = useSelector((state) => state.provinces.provinces);
    useEffect(() => {
        // Gi action  ly danh sch tnh thnh khi component c to
        dispatch(fetchProvinces());
    }, [dispatch]);

    // State Modal Thm khch hng
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [customerFormData, setCustomerFormData] = useState({
        fullName: '',
        phone: '',
        email: '',
        address: '',
        city: '',
        district: '',
        country: "Vietnam"
    });

    const [selectedCity, setSelectedCity] = useState(null); // Thm state  lu tr thnh ph  chn
    const [districts, setDistricts] = useState([]); // State lu danh sch qun/huyn
    const [selectedDistrict, setSelectedDistrict] = useState('');

    //State Modal Sa Sn phm
    const [isEditModalOpen, setIsEditModalOpen] = useState(false);
    const [currentEditCustomer, setCurrentEditCustomer] = useState(null);

    // Hm x l thay i trn cc trng input
    const handleInputChange = (event) => {
        setCustomerFormData({ ...customerFormData, [event.target.name]: event.target.value });
    };
    // Hm x l m Modal
    const handleOpenModal = () => {
        setIsModalOpen(true);
    };
    //Hm x l khi n Confirm trn Modal
    const handleConfirm = async () => {
        // Kim tra xem email v phone  c in y  hay khng
        const isComplete = customerFormData.email !== '' && customerFormData.phone !== '';

        if (!isComplete) {
            alert('Xin hy nhp y  Email v S in thoi!');
            return;
        }

        try {
            // To mt object cha thng tin khch hng t customerFormData
            const customerData = {
                fullName: customerFormData.fullName,
                phone: customerFormData.phone,
                email: customerFormData.email,
                address: customerFormData.address,
                city: selectedCity,
                district: selectedDistrict,
                country: customerFormData.country
            };

            // Gi yu cu POST ti API  to khch hng
            const response = await fetch('http://localhost:8000/customer/customerTable', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(customerData),
            });

            if (response.ok) {
                // Nu request thnh cng
                alert('Customer created successfully');
                fetchCustomers(); // Gi hm fetchCustomers  cp nht danh sch khch hng
            } else {
                // Nu c li trong request
                const errorData = await response.json();
                console.error('Error:', errorData.message);
                alert(errorData.message || 'C li xy ra khi to khch hng');
            }
        } catch (error) {
            console.error('Error:', error);
            alert(error.message || 'C li xy ra khi to khch hng');
        }
    };


    const fetchCustomers = async () => {
        // API call to fetch customers
        const response = await fetch("http://localhost:8000/customer");
        const data = await response.json();
        setCustomers(data.result);
        setFilteredCustomers(data.result);
    };

    useEffect(() => {
        fetchCustomers();
    }, []);

    //Hm x l khi tm kim b lc
    const handleSearchChange = (e) => {
        const value = e.target.value.toLowerCase();
        setSearchTerm(value);
        const filtered = customers.filter(customer => {
            const customerName = customer.fullName ? customer.fullName.toLowerCase() : '';
            const customerPhone = customer.phone ? customer.phone.toLowerCase() : '';
            const customerEmail = customer.email ? customer.email.toLowerCase() : '';

            return customerName.includes(value) || customerPhone.includes(value) || customerEmail.includes(value);
        });
        setFilteredCustomers(filtered);
    };

    // Hm x l khi n nt Sa v Xa
    // Hm  x l s kin khi nhn nt Sa
    const handleEdit = (customer) => {
        setCurrentEditCustomer(customer);
        setIsEditModalOpen(true);
    };
    //Hm x l khi n nt Confirm trn Modal Sa
    const handleUpdateConfirm = () => {
        fetch(`http://localhost:8000/customer/${currentEditCustomer._id}`, {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(currentEditCustomer)
        })
            .then(response => {
                if (!response.ok) {
                    throw new Error('C li xy ra khi cp nht khch hng');
                }
                return response.json();
            })
            .then(data => {
                alert('Khch hng  c cp nht thnh cng');
                setIsEditModalOpen(false);
                // Cp nht li danh sch sn phm
                fetchCustomers();
            })
            .catch(error => {
                console.error('Error:', error);
                alert(error.message);
            });
    };

    //Hm x l khi n nt Xa
    // Hm  x l s kin khi nhn nt Xa
    const handleDelete = (customerId) => {
        // Gi yu cu  kim tra n hng ca khch hng
        fetch(`http://localhost:8000/order/customer?customerId=${customerId}`)
            .then(response => {
                if (!response.ok) {
                    throw new Error('Khng th kim tra n hng ca khch hng');
                }
                return response.json();
            })
            .then(data => {
                // Nu c n hng cha customerId tng ng, thng bo khng th xa
                if (data.message === 'Khch hng ny c cc n hng lin quan, khng th xa!') {
                    alert(data.message);
                } else {
                    // Nu khng c n hng no cha customerId tng ng, hi xc nhn trc khi xa
                    if (window.confirm("Bn c chc chn mun xa khch hng ny khng?")) {
                        // Gi yu cu  xa khch hng
                        fetch(`http://localhost:8000/customer/${customerId}`, {
                            method: 'DELETE',
                            headers: { 'Content-Type': 'application/json' },
                        })
                            .then(response => {
                                if (!response.ok) {
                                    throw new Error('C li xy ra khi xa khch hng');
                                }
                                return response.json();
                            })
                            .then(() => {
                                alert('Khch hng  c xa thnh cng');
                                fetchCustomers();
                            })
                            .catch(error => {
                                console.error('Error:', error);
                                alert(error.message);
                            });
                    } else {
                        throw new Error('Hy b thao tc xa');
                    }
                }
            })
            .catch(error => {
                console.error('Error:', error);
                alert(error.message);
            });
    };

    const customFontStyle = {
        fontFamily: "'Happy Monkey', sans-serif",
    };
    const modalStyle = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 400,
        bgcolor: '#fef9cc',
        boxShadow: 24,
        p: 4, // Padding
    };
    //Button Confirm Style
    const buttonConfirmStyle = {
        backgroundColor: '#6c8e5d',
        '&:hover': {
            backgroundColor: '#388E3C',
        },
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: 'large',
        color: '#fef7d0',
        fontweight: 'bold',
        cursor: 'pointer',
        margin: '10px 0',
    };
    // Button Cancel Style
    const buttonCancelStyle = {
        backgroundColor: '#fcba03',
        '&:hover': {
            backgroundColor: '#ff0011',
        },
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: 'large',
        color: '#01723e',
        fontweight: 'bold',
        cursor: 'pointer',
        margin: '20px 0',
    };

    // Hm ny s c gi khi tnh/thnh ph c chn thay i
    const handleCityChange = (event) => {
        const value = event.target.value; // Ly gi tr  chn t s kin onChange
        setSelectedCity(value); // Cp nht gi tr ca selectedCity

        // Tm danh sch qun/huyn da trn tnh/thnh ph c chn
        const selectedProvince = provinces.find(province => province.name === value);

        if (selectedProvince && selectedProvince.districts) {
            setDistricts(selectedProvince.districts); // Cp nht danh sch qun/huyn
            setSelectedDistrict(''); // Reset gi tr qun/huyn  chn
        } else {
            setDistricts([]); // Nu khng tm thy danh sch, t danh sch qun/huyn rng
            setSelectedDistrict(''); // Reset gi tr qun/huyn  chn
        }
    };

    return (
        <Box sx={{ ...customFontStyle, padding: 2 }}>
            {/* Modal Thm khch hng*/}
            <Modal open={isModalOpen} onClose={() => setIsModalOpen(false)}>
                <Box sx={modalStyle}>
                    <Typography variant="h5" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.6rem' }}>Thm Khch Hng</Typography>
                    <TextField label="H tn" name="fullName" onChange={handleInputChange} fullWidth margin="normal" />
                    <TextField label="S in thoi" name="phone" onChange={handleInputChange} fullWidth margin="normal" />
                    <TextField label="Email" name="email" onChange={handleInputChange} fullWidth margin="normal" />
                    <TextField label="a ch" name="address" onChange={handleInputChange} fullWidth margin="normal" />
                    <FormControl fullWidth>
                        <InputLabel>Thnh ph</InputLabel>
                        <Select
                            name="city"
                            value={selectedCity}
                            onChange={handleCityChange}
                        >
                            {provinces.map((province) => (
                                <MenuItem key={province.code} value={province.name}>
                                    {province.name}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    <FormControl fullWidth>
                        <InputLabel>Qun</InputLabel>
                        <Select
                            name="district"
                            value={selectedDistrict}
                            onChange={(e) => setSelectedDistrict(e.target.value)}
                        >
                            {districts.map((district) => (
                                <MenuItem key={district.code} value={district.name}>
                                    {district.name}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    <FormControl fullWidth>
                        <Typography>{customerFormData.country}</Typography>
                        <InputLabel>Quc Gia</InputLabel>
                    </FormControl>
                    {/* Ct Action */}
                    <Button onClick={handleConfirm} style={buttonConfirmStyle}>Xc Nhn</Button>
                    <Button onClick={() => setIsModalOpen(false)} style={buttonCancelStyle}>Hy</Button>
                </Box>
            </Modal>
            {/* Modal Sa Khch hng*/}
            <Modal open={isEditModalOpen} onClose={() => setIsEditModalOpen(false)}>
                <Box sx={modalStyle}>
                    <Typography variant="h5" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.6rem' }}>Sa Thng Tin Khch Hng - ID: {currentEditCustomer?._id}</Typography>
                    <TextField
                        label="H Tn"
                        name="fullName"
                        value={currentEditCustomer?.fullName}
                        onChange={e => setCurrentEditCustomer({ ...currentEditCustomer, fullName: e.target.value })}
                        fullWidth margin="normal" />
                    <TextField
                        label="S in thoi"
                        name="phone"
                        value={currentEditCustomer?.phone}
                        onChange={e => setCurrentEditCustomer({ ...currentEditCustomer, phone: e.target.value })}
                        fullWidth margin="normal" />
                    <TextField
                        label="Email"
                        name="email"
                        value={currentEditCustomer?.email}
                        onChange={e => setCurrentEditCustomer({ ...currentEditCustomer, email: e.target.value })}
                        fullWidth margin="normal" />
                    <TextField
                        label="a ch"
                        name="address"
                        value={currentEditCustomer?.address}
                        onChange={e => setCurrentEditCustomer({ ...currentEditCustomer, address: e.target.value })}
                        fullWidth margin="normal" />
                    <FormControl fullWidth>
                        <InputLabel>Thnh ph</InputLabel>
                        <Select
                            name="city"
                            value={selectedCity}
                            onChange={handleCityChange}
                        >
                            {provinces.map((province) => (
                                <MenuItem key={province.code} value={province.name}>
                                    {province.name}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    <FormControl fullWidth>
                        <InputLabel>Qun</InputLabel>
                        <Select
                            name="district"
                            value={selectedDistrict}
                            onChange={(e) => setSelectedDistrict(e.target.value)}
                        >
                            {districts.map((district) => (
                                <MenuItem key={district.code} value={district.name}>
                                    {district.name}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    <FormControl fullWidth>
                        <Typography>{customerFormData.country}</Typography>
                        <InputLabel>Quc Gia</InputLabel>
                    </FormControl>
                    <Button onClick={handleUpdateConfirm} style={buttonConfirmStyle}>Cp Nht</Button>
                    <Button onClick={() => setIsEditModalOpen(false)} style={buttonCancelStyle}>Hy</Button>
                </Box>
            </Modal>
            {/* Lc khch hng */}
            <Box
                border={1}
                borderColor="purple"
                borderRadius={4}
                p={2}
                mb={2}
                style={{ backgroundColor: 'lavender', width: '33%' }}
            >
                <Typography variant="h5" color="purple" style={{ ...customFontStyle, margin: '5px 0', fontWeight: 'bold' }}>Lc Khch Hng</Typography>
                <TextField label="H tn, s in thoi v/hoc email" value={searchTerm} onChange={handleSearchChange} fullWidth style={{ width: '50%' }} />
            </Box>
            {/* Bng hin th danh sch khch hng */}
            <Paper elevation={3} sx={{ margin: 2, textAlign: 'center', padding: 2 }}>
                <Typography variant="h3" component="h2" sx={{ ...customFontStyle, marginBottom: 2 }}>
                    Danh sch khch hng
                </Typography>
                {/* Nt m Modal */}
                <Box sx={{ display: 'flex', justifyContent: 'center', marginBottom: 2 }}>
                    <Button onClick={handleOpenModal} variant="contained" color="success">
                        Thm Khch hng
                    </Button>
                </Box>
            </Paper>
            <Paper elevation={3} sx={{ margin: 2 }}>
                <Grid container>
                    <Grid item xs={12}>
                        <TableContainer component={Paper} sx={{ overflowX: 'auto' }}>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.1rem' }}>ID</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.1rem' }}>H tn</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.1rem' }}>Email</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.1rem' }}>S in thoi</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.1rem' }}>a ch</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.1rem' }}>Thnh ph</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.1rem' }}>Quc gia</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.1rem' }}>Actions</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {Array.isArray(filteredCustomers) && filteredCustomers.length > 0 ? (
                                        filteredCustomers.map(customer => (
                                            <TableRow key={customer._id}>
                                                <TableCell>{customer._id}</TableCell>
                                                <TableCell>{customer.fullName}</TableCell>
                                                <TableCell>{customer.email}</TableCell>
                                                <TableCell>{customer.phone}</TableCell>
                                                <TableCell>{customer.address}</TableCell>
                                                <TableCell>{customer.city}</TableCell>
                                                <TableCell>{customer.country}</TableCell>
                                                <TableCell>
                                                    <Button onClick={() => handleEdit(customer)} style={{ backgroundColor: 'green', color: 'white', borderRadius: '5px' }}>Sa</Button>
                                                    <Button onClick={() => handleDelete(customer._id)} style={{ backgroundColor: 'red', color: 'white', borderRadius: '5px' }}>Xa</Button>
                                                </TableCell>
                                            </TableRow>
                                        ))
                                    ) : (
                                        <TableRow>
                                            <TableCell colSpan={9}>No customers found.</TableCell>
                                        </TableRow>
                                    )}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Grid>
                </Grid>
            </Paper>
        </Box>
    );
};

export default CustomerTable;
