const initialState = {
    isLoggedIn: !!sessionStorage.getItem('token'), // Kim tra xem c token trong storage khng
    token: sessionStorage.getItem('token') || null, //Ly token t sessionStorage 
    userDetails: {},
    userRole: JSON.parse(sessionStorage.getItem('userRole')) || null
};

const userReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'USER_LOGIN':
            sessionStorage.setItem('token', action.payload.token); // Lu token vo sessionStorage
            return {
                ...state,
                isLoggedIn: true,
                token: action.payload.token,
                userDetails: action.payload.userDetails,
                userRole: action.payload.userRole
            };
        case 'USER_LOGOUT':
            sessionStorage.removeItem('token'); // Xa token khi sessionStorage
            return {
                ...state,
                isLoggedIn: false,
                token: null,
                userDetails: {},
                userRole: null
            };
        default:
            return state;
    }
};

export default userReducer;
