import * as React from 'react';
import { Button, TextField, Grid, Link, Paper, Typography, Box } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import Logo from './header/logo/Logo';
import { useDispatch } from 'react-redux';
import { loginUser } from '../actions/actions';

export default function Login() {
    const dispatch = useDispatch();
    const [username, setUsername] = React.useState('');
    const [password, setPassword] = React.useState('');
    const navigate = useNavigate();


    const handleSubmit = (event) => {
        event.preventDefault();

        // Gi API ng nhp
        fetch('http://localhost:8000/auth/login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ username, password })
        })
            .then(response => {
                if (!response.ok) {
                    if (response.status === 404) {
                        throw new Error('Khng tm thy User');
                    }
                    if (response.status === 401) {
                        throw new Error('Mt khu khng ng');
                    }
                    throw new Error('ng nhp tht bi!');
                }
                return response.json();
            })
            .then(data => {
                // Lu token v refreshToken vo sessionStorage
                sessionStorage.setItem('token', data.accessToken);
                sessionStorage.setItem('refreshToken', data.refreshToken);

                // Kim tra xem data c userId khng trc khi lu
                if (data.userId) {
                    sessionStorage.setItem('userId', data.userId);
                }
                if (data.userRole) {
                    sessionStorage.setItem('userRole', JSON.stringify(data.userRole));
                }

                // Lu thng tin khch hng vo sessionStorage (nu c)
                if (data.customerInfo) {
                    sessionStorage.setItem('customerInfo', JSON.stringify(data.customerInfo));
                }

                // t trng thi ng nhp trong sessionStorage
                sessionStorage.setItem('isLoggedIn', true);
                // Dispatch hnh ng ng nhp ti Redux store
                dispatch(loginUser({
                    token: data.accessToken,
                    refreshToken: data.refreshToken,
                    userRole: data.userRole,
                    customerInfo: data.customerInfo
                }));
                // Chuyn hng ngi dng sau khi ng nhp thnh cng
                navigate("/");
            })
            .catch(error => {
                console.error('Error:', error);
                if (error.message === 'Khng tm thy ngi dng') {
                    alert('Khng tm thy ngi dng. Xin hy th li.');
                }
                else {
                    alert(error.message);
                }
            });
    };

    const customFontStyle = {
        fontFamily: "'Happy Monkey', sans-serif",
    };

    const buttonStyle = {
        backgroundColor: '#07361f',
        '&:hover': {
            backgroundColor: '#6c8e5d',
        },
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: 'large',
        color: '#fef7d0',
        fontweight: 'bold',
        cursor: 'pointer',
        margin: '20px 0',
    };

    return (
        <Box display="flex" justifyContent="flex-end" m={2}>
            <Paper style={{ padding: 20, maxWidth: 300, margin: "0 auto", backgroundColor: '#fef7d0' }}>
                <Logo />
                <Typography
                    variant="h5"
                    style={{ marginBottom: 20 }}
                    sx={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.6rem' }}
                >
                    ng nhp
                </Typography>
                <form onSubmit={handleSubmit}>
                    <Grid container spacing={2} direction="column">
                        <Grid item>
                            <TextField
                                fullWidth
                                label="Username"
                                variant="outlined"
                                required
                                value={username}
                                onChange={(e) => setUsername(e.target.value)}
                            />
                        </Grid>
                        <Grid item>
                            <TextField
                                fullWidth
                                label="Password"
                                type="password"
                                variant="outlined"
                                required
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                            />
                        </Grid>
                        <Grid item>
                            <Button type="submit" variant="contained" color="primary" fullWidth sx={buttonStyle}>
                                ng nhp
                            </Button>
                        </Grid>
                        <Grid item>
                            <Typography variant="body2" sx={{ ...customFontStyle, fontSize: '1rem' }}>
                                Cha c ti khon? <Link href="/happy-tree-ecommerce/signup">ng k</Link>
                            </Typography>
                        </Grid>
                    </Grid>
                </form>
            </Paper>
        </Box>

    );
}
