import React, { useEffect, useState, useCallback } from "react";
import { useSelector, useDispatch } from 'react-redux';
import { Paper, MenuItem, Card, CardContent, Grid, Button, Modal, Table, TableBody, TableCell, TableHead, TableRow, TextField, TableContainer, Box, Typography, FormControl, InputLabel, Select } from '@mui/material';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { fetchProvinces } from "../../../actions/actions";


const OrderTable = () => {
    const dispatch = useDispatch();
    const [orders, setOrders] = useState([]);
    const [filteredOrders, setFilteredOrders] = useState(orders);
    const [filterDate, setFilterDate] = useState(null);
    const [hasFiltered, setHasFiltered] = useState(false);

    const provinces = useSelector((state) => state.provinces.provinces);
    useEffect(() => {
        // Gi action  ly danh sch tnh thnh khi component c to
        dispatch(fetchProvinces());
    }, [dispatch]);

    // State Modal Thm Order
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [orderFormData, setOrderFormData] = useState({
        orderDetails: {
            products: [{
                productId: '',
                quantity: 1
            }],
            totalQuantity: 0,
            total: 0
        },
        order: {
            orderDate: new Date().toISOString().split('T')[0], // Format ngy hin ti thnh YYYY-MM-DD
            cost: 0
        },
        customer: {
            fullName: '',
            phone: '',
            email: '',
            address: '',
            city: '',
            district: '',
            country: 'Vietnam'
        }
    });

    const [selectedCity, setSelectedCity] = useState(null); // Thm state  lu tr thnh ph  chn
    const [districts, setDistricts] = useState([]); // State lu danh sch qun/huyn
    const [selectedDistrict, setSelectedDistrict] = useState('');

    //State  lu danh sch sn phm
    const [products, setProducts] = useState([]);
    //State Modal Sa Sn phm
    const [isEditModalOpen, setIsEditModalOpen] = useState(false);
    const [currentEditOrder, setCurrentEditOrder] = useState(null);

    // Hm x l m Modal Thm
    const handleOpenModal = () => {
        // t gi tr mc nh cho orderDate v cost khi m Modal
        setOrderFormData({
            ...orderFormData,
            order: {
                ...orderFormData.order,
                orderDate: new Date().toISOString().split('T')[0], // Ngy hin ti
                cost: 0 // Cost mc nh
            }
        });
        setIsModalOpen(true);
    };
    //Hm x l khi n Confirm trn Modal Thm
    const handleConfirm = async () => {
        try {
            // To orderDetails mi
            const orderDetailsResponse = await fetch('http://localhost:8000/orderDetail', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(orderFormData.orderDetails)
            });
            const orderDetailsData = await orderDetailsResponse.json();

            // To customer mi
            const customerResponse = await fetch('http://localhost:8000/customer/customerTable', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    fullName: orderFormData.customer.fullName,
                    phone: orderFormData.customer.phone,
                    email: orderFormData.customer.email,
                    address: orderFormData.customer.address,
                    city: selectedCity,
                    district: selectedDistrict,
                    country: orderFormData.customer.country
                })
            });
            const customerData = await customerResponse.json();

            // To order mi vi orderDetailsId v customerId mi to
            await fetch('http://localhost:8000/order', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    orderDate: orderFormData.order.orderDate,
                    orderDetails: orderDetailsData.result._id,
                    cost: orderFormData.order.cost,
                    customer: customerData.customer._id
                })
            });
            // X l phn hi v cp nht UI
            alert("n hng  c thm thnh cng!");
            fetchOrders();
            setIsModalOpen(false);
        } catch (error) {
            console.error('Error:', error);
            alert("C li xy ra khi thm n hng.");
        }
    };

    // Hm ly danh sch order
    const fetchOrders = useCallback(async () => {
        // Nu filterDate l null, t hasFiltered v false v ly tt c n hng
        if (!filterDate) {
            setHasFiltered(false);
        }

        // i nh dng ngy thng
        let dateString = '';
        if (filterDate) {
            // Chuyn i ngy sang nh dng YYYY-MM-DD m khng thay i mi gi
            dateString = `${filterDate.getFullYear()}-${String(filterDate.getMonth() + 1).padStart(2, '0')}-${String(filterDate.getDate()).padStart(2, '0')}`;
        }

        const endpoint = dateString ? `date?date=${dateString}` : '';
        try {
            const response = await fetch(`http://localhost:8000/order/${endpoint}`);
            let data = await response.json();


            if (data.result) {
                setOrders(data.result);
                setFilteredOrders(data.result);
            } else if (Array.isArray(data)) {
                setOrders(data);
                setFilteredOrders(data);
            } else {
                console.error('Unexpected response format:', data);
                setOrders([]);
                setFilteredOrders([]);
            }
        } catch (error) {
            console.error('Error fetching orders:', error);
            setOrders([]);
            setFilteredOrders([]);
        }
    }, [filterDate]);
    // useEffect  ly danh sch order
    useEffect(() => {
        fetchOrders(); // Gi fetchOrders ngay khi component c mount
    }, []);

    // useEffect  ly danh sch sn phm
    useEffect(() => {
        fetch('http://localhost:8000/product')
            .then(response => response.json())
            .then(data => setProducts(data.data))
            .catch(error => console.error('C li khi ti sn phm:', error));
    }, []);

    // Hm chuyn i nh dng ngy thng YYYY-MM-DD
    function formatDateToYYYYMMDD(date) {
        const year = date.getFullYear();
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const day = String(date.getDate()).padStart(2, '0');
        return `${year}-${month}-${day}`;
    }

    //Hm x l khi thay i sn phm trn Modal Thm
    const handleProductChange = (index, event) => {
        const newProducts = orderFormData.orderDetails.products.map((product, i) => {
            if (i === index) {
                return { ...product, productId: event.target.value };
            }
            return product;
        });
        setOrderFormData({ ...orderFormData, orderDetails: { ...orderFormData.orderDetails, products: newProducts } });
    };
    // Hm x l khi thay i s lng sn phm trn Modal Thm
    const handleQuantityChange = (index, event) => {
        const newProducts = orderFormData.orderDetails.products.map((product, i) => {
            if (i === index) {
                return { ...product, quantity: parseInt(event.target.value, 10) };
            }
            return product;
        });
        setOrderFormData({ ...orderFormData, orderDetails: { ...orderFormData.orderDetails, products: newProducts } });
    };
    // Hm x l khi n du + trn Modal Thm
    const addNewProduct = () => {
        const newProduct = { productId: '', quantity: 1 };
        setOrderFormData({
            ...orderFormData,
            orderDetails: {
                ...orderFormData.orderDetails,
                products: [...orderFormData.orderDetails.products, newProduct]
            }
        });
    };
    // Hm  tnh tng gi sn phm v hin th trn gi ca order trn Modal Thm
    const calculateTotals = () => {
        let totalQuantity = 0;
        let totalCost = 0;

        orderFormData.orderDetails.products.forEach(product => {
            const productData = products.find(p => p._id === product.productId);
            if (productData) {
                totalQuantity += product.quantity;
                totalCost += product.quantity * productData.buyPrice;
            }
        });

        setOrderFormData(prevState => ({
            ...prevState,
            orderDetails: {
                ...prevState.orderDetails,
                totalQuantity: totalQuantity,
                total: totalCost
            },
            order: {
                ...prevState.order,
                cost: totalCost
            }
        }));
    };
    // useEffect cho vic tnh ton tng gi sn phm trn Modal Thm
    useEffect(() => {
        calculateTotals();
    }, [orderFormData.orderDetails.products]);

    // Hm  x l s kin khi nhn nt Sa
    const handleEdit = async (order) => {
        try {
            const orderDetailsResponse = await fetch(`http://localhost:8000/orderDetail/${order.orderDetails}`);
            const orderDetails = await orderDetailsResponse.json();

            const customerResponse = await fetch(`http://localhost:8000/customer/${order.customer}`);
            const customer = await customerResponse.json();

            setCurrentEditOrder({
                ...order,
                orderDetails: orderDetails.result || [],
                customer: customer.result || []
            });
            setIsEditModalOpen(true);
        } catch (error) {
            console.error('C li khi ti thng tin n hng v khc hng:', error);
            alert('Tht bi khi ti thng tin n hng v khc hng');
        }
    };
    //Hm x l khi n nt Confirm trn Modal Sa
    const handleUpdateConfirm = async () => {
        try {
            // Gi API  cp nht thng tin sn phm trong orderDetails
            await fetch(`http://localhost:8000/orderDetail/${currentEditOrder.orderDetails._id}`, {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ products: currentEditOrder.orderDetails.products })
            });

            // Gi API  cp nht thng tin customer
            await fetch(`http://localhost:8000/customer/${currentEditOrder.customer._id}`, {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({
                    fullName: currentEditOrder.customer.fullName,
                    phone: currentEditOrder.customer.phone,
                    email: currentEditOrder.customer.email,
                    address: currentEditOrder.customer.address,
                    city: selectedCity,
                    district: selectedDistrict,
                    country: currentEditOrder.customer.country
                })
            });

            alert('n hng thng tin khch hng c cp nht thnh cng!');
            setIsEditModalOpen(false);
            // Cp nht li danh sch n hng nu cn
            fetchOrders();
        } catch (error) {
            console.error('C li khi cp nht n hng v thng tin khch hng:', error);
            alert('Tht bi khi cp nht n hng v thng tin khch hng');
        }
    };

    // Hm  x l s kin khi nhn nt Xa
    const handleDelete = async (orderId) => {
        // Hin th thng bo xc nhn
        const isConfirmed = window.confirm("Bn c chc chn mun xa n hng ny khng?");

        if (isConfirmed) {
            // Ngi dng xc nhn xa
            try {
                const response = await fetch(`http://localhost:8000/order/${orderId}`, {
                    method: 'DELETE',
                    headers: { 'Content-Type': 'application/json' }
                });

                if (!response.ok) {
                    throw new Error('C li xy ra khi xa n hng');
                }

                // X l thnh cng, thng bo cho ngi dng v cp nht UI
                alert('n hng  c xa thnh cng');
                fetchOrders();
                // Cp nht danh sch n hng, v d: fetchOrders();
            } catch (error) {
                console.error('Error:', error);
                alert('C li xy ra khi xa n hng');
            }
        }
        // Ngi dng khng xc nhn xa
    };

    const customFontStyle = {
        fontFamily: "'Happy Monkey', sans-serif",
    };
    const modalStyle = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 400,
        bgcolor: '#fef9cc',
        boxShadow: 24,
        p: 4, // Padding
    };
    //Button Confirm Style
    const buttonConfirmStyle = {
        backgroundColor: '#6c8e5d',
        '&:hover': {
            backgroundColor: '#388E3C',
        },
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: 'large',
        color: '#fef7d0',
        fontweight: 'bold',
        cursor: 'pointer',
        margin: '10px 0',
    };
    // Button Cancel Style
    const buttonCancelStyle = {
        backgroundColor: '#fcba03',
        '&:hover': {
            backgroundColor: '#ff0011',
        },
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: 'large',
        color: '#01723e',
        fontweight: 'bold',
        cursor: 'pointer',
        margin: '20px 0',
    };

    // Hm ny s c gi khi tnh/thnh ph c chn thay i
    const handleCityChange = (event) => {
        const value = event.target.value; // Ly gi tr  chn t s kin onChange
        setSelectedCity(value); // Cp nht gi tr ca selectedCity

        // Tm danh sch qun/huyn da trn tnh/thnh ph c chn
        const selectedProvince = provinces.find(province => province.name === value);

        if (selectedProvince && selectedProvince.districts) {
            setDistricts(selectedProvince.districts); // Cp nht danh sch qun/huyn
            setSelectedDistrict(''); // Reset gi tr qun/huyn  chn
        } else {
            setDistricts([]); // Nu khng tm thy danh sch, t danh sch qun/huyn rng
            setSelectedDistrict(''); // Reset gi tr qun/huyn  chn
        }
    };

    return (
        <Box sx={{ ...customFontStyle, padding: 2 }}>
            {/* Modal Thm n Hng */}
            <Modal open={isModalOpen} onClose={() => setIsModalOpen(false)}>
                <Box sx={modalStyle}>
                    <Typography variant="h5" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.6rem' }}>Thm n Hng Mi</Typography>
                    {/* Form nhp thng tin sn phm */}
                    <Typography variant="h6" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.4rem' }}>Thng Tin Sn Phm</Typography>
                    {orderFormData.orderDetails.products.map((product, index) => (
                        <Box key={index} display="flex" alignItems="center" mb={2}>
                            <TextField
                                select
                                label="Product"
                                value={product.productId}
                                onChange={(event) => handleProductChange(index, event)}
                                margin="normal"
                                sx={{ marginRight: '10px', width: '70%' }}
                            >
                                {products.map((p) => (
                                    <MenuItem key={p._id} value={p._id}>
                                        {p.name}
                                    </MenuItem>
                                ))}
                            </TextField>
                            <TextField
                                label="Quantity"
                                type="number"
                                value={product.quantity}
                                onChange={(event) => handleQuantityChange(index, event)}
                                margin="normal"
                                sx={{ width: '30%' }}
                            />
                        </Box>
                    ))}
                    <Button
                        onClick={addNewProduct}
                        color="primary"
                        variant="contained"
                        style={{ marginTop: '10px', borderRadius: '50%', minWidth: '50px', height: '50px' }}
                    >
                        +
                    </Button>
                    {/* Hin th Total Quantity */}
                    <Typography variant="subtitle1" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.4rem' }}>
                        Total Quantity: {orderFormData.orderDetails.totalQuantity}
                    </Typography>

                    {/* Form nhp thng tin khch hng */}
                    <Typography variant="h6" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.6rem' }}>Thng Tin Khch Hng</Typography>
                    <TextField
                        label="H tn"
                        value={orderFormData.customer.fullName}
                        onChange={(e) => setOrderFormData({ ...orderFormData, customer: { ...orderFormData.customer, fullName: e.target.value } })}
                        margin="normal"
                        fullWidth
                    />
                    <TextField
                        label="S in thoi"
                        value={orderFormData.customer.phone}
                        onChange={(e) => setOrderFormData({ ...orderFormData, customer: { ...orderFormData.customer, phone: e.target.value } })}
                        margin="normal"
                        fullWidth
                    />
                    <TextField
                        label="Email"
                        value={orderFormData.customer.email}
                        onChange={(e) => setOrderFormData({ ...orderFormData, customer: { ...orderFormData.customer, email: e.target.value } })}
                        margin="normal"
                        fullWidth
                    />
                    <FormControl fullWidth >
                        <InputLabel>Thnh ph</InputLabel>
                        <Select
                            name="city"
                            value={selectedCity}
                            onChange={handleCityChange}
                        >
                            {provinces.map((province) => (
                                <MenuItem key={province.code} value={province.name}>
                                    {province.name}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    <FormControl fullWidth>
                        <InputLabel>Qun</InputLabel>
                        <Select
                            name="district"
                            value={selectedDistrict}
                            onChange={(e) => setSelectedDistrict(e.target.value)}
                        >
                            {districts.map((district) => (
                                <MenuItem key={district.code} value={district.name}>
                                    {district.name}
                                </MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    <br/>
                    <FormControl fullWidth>
                        <Typography>Quc Gia: {orderFormData.customer.country}</Typography>
                    </FormControl>
                    {/* Hin th thng tin OrderDate v Cost */}
                    <Typography variant="subtitle1" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.4rem' }}>Order Date: {orderFormData.order.orderDate}</Typography>
                    <Typography variant="subtitle1" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.4rem' }}>Cost: {orderFormData.order.cost}</Typography>

                    <Box style={{ marginTop: '20px' }}>
                        <Button onClick={handleConfirm} style={buttonConfirmStyle}>Xc Nhn</Button>
                        <Button onClick={() => setIsModalOpen(false)} style={buttonCancelStyle}>Hy</Button>
                    </Box>
                </Box>
            </Modal>
            {/* Modal Sa sn phm*/}
            {currentEditOrder && (
                <Modal open={isEditModalOpen} onClose={() => setIsEditModalOpen(false)}>
                    <Box sx={modalStyle}>
                        <Typography variant="h5" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.6rem' }}>Sa n Hng - ID: {currentEditOrder._id}</Typography>
                        <Typography variant="h5" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.4rem' }}>Ngy To: {currentEditOrder.orderDate}</Typography>
                        {/* Hin th thng tin chi tit ca OrderDetails */}
                        <Grid container spacing={2}>
                            {/* Phn Edit Product */}
                            <Grid item xs={12} md={6}>
                                <Card>
                                    <CardContent>
                                        <Typography variant="h6" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.4rem' }}>Sa Sn Phm</Typography>
                                        {currentEditOrder.orderDetails.products.map((product, index) => (
                                            <Box key={product._id} mb={2}>
                                                <Typography variant="subtitle1" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.3rem' }}>ID Sn Phm: {product.product}</Typography>
                                                <TextField
                                                    label="S lng"
                                                    type="number"
                                                    value={product.quantity}
                                                    onChange={(e) => {
                                                        // To bn sao mi ca mng products
                                                        let newProducts = currentEditOrder.orderDetails.products.map(p => {
                                                            if (p._id === product._id) {
                                                                // Cp nht quantity cho sn phm c th
                                                                return { ...p, quantity: parseInt(e.target.value, 10) || 0 };
                                                            }
                                                            return p;
                                                        });

                                                        // Tnh ton totalQuantity v total
                                                        const newTotalQuantity = newProducts.reduce((total, product) => total + product.quantity, 0);
                                                        // Cp nht state
                                                        setCurrentEditOrder({
                                                            ...currentEditOrder,
                                                            orderDetails: {
                                                                ...currentEditOrder.orderDetails,
                                                                products: newProducts,
                                                                totalQuantity: newTotalQuantity

                                                            }
                                                        });
                                                    }}
                                                    margin="normal"
                                                    fullWidth
                                                />
                                            </Box>
                                        ))}
                                    </CardContent>
                                </Card>
                            </Grid>

                            {/* Phn Customer Information */}
                            <Grid item xs={12} md={6}>
                                <Card>
                                    <CardContent>
                                        <Typography variant="subtitle1" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.4rem' }}>Sa thng tin khch hng</Typography>
                                        <TextField
                                            label="H tn"
                                            value={currentEditOrder.customer.fullName}
                                            onChange={(e) => setCurrentEditOrder({
                                                ...currentEditOrder,
                                                customer: { ...currentEditOrder.customer, fullName: e.target.value }
                                            })}
                                            margin="normal"
                                            fullWidth
                                        />
                                        <TextField
                                            label="S in thoi"
                                            value={currentEditOrder.customer.phone}
                                            onChange={(e) => setCurrentEditOrder({
                                                ...currentEditOrder,
                                                customer: { ...currentEditOrder.customer, phone: e.target.value }
                                            })}
                                            margin="normal"
                                            fullWidth
                                        />
                                        <TextField
                                            label="Email"
                                            value={currentEditOrder.customer.email}
                                            onChange={(e) => setCurrentEditOrder({
                                                ...currentEditOrder,
                                                customer: { ...currentEditOrder.customer, email: e.target.value }
                                            })}
                                            margin="normal"
                                            fullWidth
                                        />
                                        <TextField
                                            label="a ch"
                                            value={currentEditOrder.customer.address}
                                            onChange={(e) => setCurrentEditOrder({
                                                ...currentEditOrder,
                                                customer: { ...currentEditOrder.customer, address: e.target.value }
                                            })}
                                            margin="normal"
                                            fullWidth
                                        />
                                        <FormControl fullWidth>
                                            <InputLabel>Thnh ph</InputLabel>
                                            <Select
                                                name="Thnh ph"
                                                value={selectedCity}
                                                onChange={handleCityChange}
                                            >
                                                {provinces.map((province) => (
                                                    <MenuItem key={province.code} value={province.name}>
                                                        {province.name}
                                                    </MenuItem>
                                                ))}
                                            </Select>
                                        </FormControl>
                                        <FormControl fullWidth>
                                            <InputLabel>Qun</InputLabel>
                                            <Select
                                                name="Qun"
                                                value={selectedDistrict}
                                                onChange={(e) => setSelectedDistrict(e.target.value)}
                                            >
                                                {districts.map((district) => (
                                                    <MenuItem key={district.code} value={district.name}>
                                                        {district.name}
                                                    </MenuItem>
                                                ))}
                                            </Select>
                                        </FormControl>
                                        <FormControl fullWidth>
                                            <Typography>Quc gia: {currentEditOrder.customer.country}</Typography>
                                        </FormControl>
                                    </CardContent>
                                </Card>
                            </Grid>
                        </Grid>
                        <Box mt={3}>
                            <Button onClick={handleUpdateConfirm} style={buttonConfirmStyle}>Update</Button>
                            <Button onClick={() => setIsEditModalOpen(false)} style={buttonCancelStyle}>Cancel</Button>
                        </Box>
                    </Box>
                </Modal>
            )}
            {/* Thm b lc ngy */}
            <Box
                border={1}
                borderColor="purple"
                borderRadius={4}
                p={2}
                mb={2}
                style={{ backgroundColor: 'lavender', width: '33%' }}
            >
                <Typography variant="h5" color="purple" style={{ ...customFontStyle, margin: '5px 0', fontWeight: 'bold' }}>Lc n Hng Theo Ngy</Typography>
                <DatePicker
                    selected={filterDate}
                    onChange={(date) => {
                        setFilterDate(date);
                        if (!date) {
                            setHasFiltered(false);
                            fetchOrders();
                        }
                    }}
                />
                <Button onClick={() => {
                    setHasFiltered(true);
                    fetchOrders();
                }}
                    variant="contained"
                    style={buttonConfirmStyle}>
                    Lc
                </Button>
            </Box>
            {/* Bng hin th danh sch n hng */}
            <Paper elevation={3} sx={{ margin: 2, textAlign: 'center', padding: 2 }}>
                <Typography variant="h3" component="h2" sx={{ ...customFontStyle, marginBottom: 2 }}>
                    Danh sch n hng
                </Typography>
                {/* Nt m Modal */}
                <Box sx={{ display: 'flex', justifyContent: 'center', marginBottom: 2 }}>
                    <Button onClick={handleOpenModal} variant="contained" color="success">
                        Thm n Hng
                    </Button>
                </Box>
            </Paper>
            <Paper elevation={3} sx={{ margin: 2 }}>
                <Grid container>
                    <Grid item xs={12}>
                        <TableContainer component={Paper} sx={{ overflowX: 'auto' }}>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.1rem' }}>ID</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.1rem' }}>Ngy to n hng</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.1rem' }}>Chi tit n hng</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.1rem' }}>Gi</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.1rem' }}>Khch hng</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {(hasFiltered ? filteredOrders : orders).length > 0 ? (
                                        (hasFiltered ? filteredOrders : orders).map(order => {
                                            // Log ra thng tin ca mi order
                                            return (
                                                <TableRow key={order._id}>
                                                    <TableCell>{order._id}</TableCell>
                                                    <TableCell>{formatDateToYYYYMMDD(new Date(order.orderDate))}</TableCell>
                                                    <TableCell>
                                                        {order.orderDetails.map(detailId => (
                                                            <div key={detailId}>
                                                                OrderDetails ID: {detailId}
                                                            </div>
                                                        ))}
                                                    </TableCell>
                                                    <TableCell>{order.cost}</TableCell>
                                                    <TableCell>
                                                        {order.customer ? `${order.customer}` : 'No Customer'}
                                                    </TableCell>
                                                    {/* Cc ct khc */}
                                                    <TableCell>
                                                        <Button onClick={() => handleEdit(order)} style={{ backgroundColor: 'green', color: 'white', borderRadius: '5px' }}>Sa</Button>
                                                        <Button onClick={() => handleDelete(order._id)} style={{ backgroundColor: 'red', color: 'white', borderRadius: '5px' }}>Xa</Button>
                                                    </TableCell>
                                                </TableRow>
                                            );
                                        })
                                    ) : (
                                        <TableRow>
                                            <TableCell colSpan={6}>Khng c n hng no.</TableCell>
                                        </TableRow>
                                    )}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Grid>
                </Grid>
            </Paper>
        </Box>
    );
};

export default OrderTable;

