import React from 'react';
import { useEffect, useState } from "react";
import { Paper, Grid, FormControl, Button, Modal, Select, MenuItem, InputLabel, Table, TableBody, TableCell, TableHead, TableRow, TextField, Slider, FormGroup, FormControlLabel, Checkbox, Box, Typography, TableContainer } from '@mui/material';


const ProductTable = () => {
    const [products, setProducts] = useState([]);
    //State lc sn phm
    const [filteredProducts, setFilteredProducts] = useState(products);
    const [searchTerm, setSearchTerm] = useState('');
    const [priceRange, setPriceRange] = useState([0, 1000]); // Gi s gi t 0 n 1000
    const [selectedCategories, setSelectedCategories] = useState([]);
    //State Modal Thm sn phm
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [productFormData, setProductFormData] = useState({
        name: '',
        type: '',
        description: '',
        imageUrl: '',
        buyPrice: '',
        promotionPrice: '',
        amount: ''
    });
    const [productTypes, setProductTypes] = useState([]);
    //State Modal Sa Sn phm
    const [isEditModalOpen, setIsEditModalOpen] = useState(false);
    const [currentEditProduct, setCurrentEditProduct] = useState(null);
    //State upload nh
    const [selectedFile, setSelectedFile] = useState(null);
    const [preview, setPreview] = useState('');

    //Khi tp c chn, cp nht trng thi v to URL xem trc:
    const handleFileSelect = (event) => {
        setSelectedFile(event.target.files[0]);
        setPreview(URL.createObjectURL(event.target.files[0]));
    };

    //Hm upload nh
    const uploadImage = () => {
        if (!selectedFile) {
            alert('Chn file trc!');
            return;
        }

        const formData = new FormData();
        formData.append('image', selectedFile);

        fetch('http://localhost:8000/api/upload', {
            method: 'POST',
            body: formData,
        })
            .then(response => response.json())
            .then(data => {
                console.log(data);
                alert('nh  c upload thnh cng');
                setProductFormData({ ...productFormData, imageUrl: `http://localhost:8000/${data.file}` });
                // Reset the selected file
                setSelectedFile(null);
                setPreview('');
            })
            .catch(error => {
                alert('Upload tht bi: ' + error.message);
            });
    };


    const fetchApi = async (url, body) => {
        const response = await fetch(url, body);

        const data = await response.json();

        return data;
    }
    //Hm gi API Loi sn phm
    useEffect(() => {
        fetchApi("http://localhost:8000/productType")
            .then(data => {
                setProductTypes(data.result);
            })
            .catch(error => console.error('Error:', error));
    }, []);

    // Hm x l thay i trn cc trng input
    const handleInputChange = (event) => {
        setProductFormData({ ...productFormData, [event.target.name]: event.target.value });
    };

    // Hm x l m Modal
    const handleOpenModal = () => {
        setIsModalOpen(true);
    };

    //Hm x l khi n Confirm trn Modal
    const handleConfirm = () => {
        // m bo tt c trng thng tin  c nhp
        if (Object.values(productFormData).some(field => field === '')) {
            alert('Vui lng nhp tt c thng tin sn phm');
            return;
        }

        // Gi yu cu ti API  to hoc cp nht sn phm
        fetch('http://localhost:8000/product', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(productFormData)
        })
            .then(response => {
                if (!response.ok) {
                    // X l trng hp li t API
                    throw new Error('Sn phm vi tn ny  tn ti');
                }
                return response.json();
            })
            .then(data => {
                // X l phn hi t server
                if (data.message.includes("cp nht")) {
                    alert('Sn phm  c cp nht');
                } else if (data.message.includes("thm thnh cng")) {
                    alert('Sn phm  c thm thnh cng');
                }
                setIsModalOpen(false);
                // Cp nht li danh sch sn phm
                fetchApi("http://localhost:8000/product")
                    .then(data => {
                        setProducts(data.data);
                        setFilteredProducts(data.data);
                    })
                    .catch(error => console.error('Error:', error));
            })
            .catch(error => {
                console.error('Error:', error);
                alert('C li xy ra khi x l sn phm');
            });
    };

    //Hm gi danh sch sn phm
    useEffect(() => {
        fetchApi("http://localhost:8000/product")
            .then((data) => {
                setProducts(data.data);
                setFilteredProducts(data.data);
            })
            .catch((error) => {
                console.log(error.message);
            })
    }, []);

    //Hm set sn phm  lc
    useEffect(() => {
        let updatedProducts = products;

        // Lc theo tn sn phm
        if (searchTerm) {
            updatedProducts = updatedProducts.filter(product =>
                product.name.toLowerCase().includes(searchTerm.toLowerCase())
            );
        }

        // Lc theo gi sn phm
        updatedProducts = updatedProducts.filter(product =>
            product.buyPrice >= priceRange[0] && product.buyPrice <= priceRange[1]
        );

        // Lc theo loi sn phm
        if (selectedCategories.length > 0) {
            updatedProducts = updatedProducts.filter(product =>
                selectedCategories.includes(product.category)
            );
        }

        setFilteredProducts(updatedProducts);
    }, [searchTerm, priceRange, selectedCategories, products]);

    //Hm x l lc sn phm theo loi
    const handleCategoryChange = (category) => {
        if (selectedCategories.includes(category)) {
            setSelectedCategories(selectedCategories.filter(c => c !== category));
        } else {
            setSelectedCategories([...selectedCategories, category]);
        }
    };

    // Hm  x l s kin khi nhn nt Sa
    const handleEdit = (product) => {
        setCurrentEditProduct(product);
        setIsEditModalOpen(true);
    };
    //Hm x l khi n nt Confirm trn Modal Sa
    const handleUpdateConfirm = () => {
        fetch(`http://localhost:8000/product/${currentEditProduct._id}`, {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(currentEditProduct)
        })
            .then(response => {
                if (!response.ok) {
                    throw new Error('C li xy ra khi cp nht sn phm');
                }
                return response.json();
            })
            .then(data => {
                alert('Sn phm  c cp nht thnh cng');
                setIsEditModalOpen(false);
                // Cp nht li danh sch sn phm
                fetchApi("http://localhost:8000/product")
                    .then(data => {
                        setProducts(data.data);
                        setFilteredProducts(data.data);
                    })
                    .catch(error => console.error('Error:', error));
            })
            .catch(error => {
                console.error('Error:', error);
                alert(error.message);
            });
    };

    // Hm  x l s kin khi nhn nt Xa
    const handleDelete = (productId) => {
        if (window.confirm("Bn c chc chn mun xa sn phm ny khng?")) {
            fetch(`http://localhost:8000/product/${productId}`, {
                method: 'DELETE',
                headers: { 'Content-Type': 'application/json' },
            })
                .then(response => {
                    if (!response.ok) {
                        throw new Error('C li xy ra khi xa sn phm');
                    }
                    return response.json();
                })
                .then(data => {
                    alert('Sn phm  c xa thnh cng');
                    // Cp nht li danh sch sn phm
                    fetchApi("http://localhost:8000/product")
                        .then(data => {
                            setProducts(data.data);
                            setFilteredProducts(data.data);
                        })
                        .catch(error => console.error('Error:', error));
                })
                .catch(error => {
                    console.error('Error:', error);
                    alert(error.message);
                });
        }
    };

    const customFontStyle = {
        fontFamily: "'Happy Monkey', sans-serif",
    };
    const modalStyle = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 400,
        bgcolor: '#fef9cc',
        boxShadow: 24,
        p: 4, // Padding
    };
    //Button Confirm Style
    const buttonConfirmStyle = {
        backgroundColor: '#6c8e5d',
        '&:hover': {
            backgroundColor: '#388E3C',
        },
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: 'large',
        color: '#fef7d0',
        fontweight: 'bold',
        cursor: 'pointer',
        margin: '10px 0',
    };
    // Button Cancel Style
    const buttonCancelStyle = {
        backgroundColor: '#fcba03',
        '&:hover': {
            backgroundColor: '#ff0011',
        },
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: 'large',
        color: '#01723e',
        fontweight: 'bold',
        cursor: 'pointer',
        margin: '20px 0',
    };

    return (
        <Box sx={{ ...customFontStyle, padding: 2 }}>
            {/* Modal Thm sn phm*/}
            <Modal open={isModalOpen} onClose={() => setIsModalOpen(false)}>
                <Box sx={modalStyle}>
                    <h2 style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.6rem' }}>Thm Sn Phm</h2>
                    <TextField label="Tn Sn Phm" name="name" onChange={handleInputChange} fullWidth margin="normal" />
                    <FormControl fullWidth margin="normal">
                        <InputLabel>Loi Sn Phm</InputLabel>
                        <Select
                            name="type"
                            value={productFormData.type}
                            onChange={handleInputChange}
                        >
                            {productTypes.map(type => (
                                <MenuItem key={type._id} value={type._id}>{type.name}</MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    <TextField label="M t" name="description" onChange={handleInputChange} fullWidth margin="normal" />
                    <TextField
                        label="ImageUrl"
                        name="imageUrl"
                        value={productFormData.imageUrl}
                        onChange={handleInputChange}
                        fullWidth
                        margin="normal"
                        InputProps={{
                            readOnly: true, // Makes the input field read-only
                        }}
                    />
                    <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', marginBottom: 2 }}>
                        {preview && (
                            <Box>
                                <img src={preview} alt="Preview" style={{ maxWidth: '100%', marginBottom: 10 }} />
                            </Box>
                        )}
                        <Button variant="contained" component="label">
                            Select Image
                            <input type="file" hidden onChange={handleFileSelect} />
                        </Button>
                        <Button
                            onClick={uploadImage}
                            variant="contained"
                            color="primary"
                            sx={{ marginTop: 2 }}
                        >
                            Upload Image
                        </Button>
                    </Box>
                    <TextField label="Gi nim yt" name="buyPrice" onChange={handleInputChange} fullWidth margin="normal" />
                    <TextField label="Gi bn" name="promotionPrice" onChange={handleInputChange} fullWidth margin="normal" />
                    <TextField label="S lng" name="amount" onChange={handleInputChange} fullWidth margin="normal" />
                    {/* Cc trng input khc: description, imageUrl, buyPrice, promotionPrice, amount */}
                    <Button onClick={handleConfirm} style={buttonConfirmStyle}>Xc Nhn</Button>
                    <Button onClick={() => setIsModalOpen(false)} style={buttonCancelStyle}>Hy</Button>
                </Box>
            </Modal>
            {/* Modal Sa sn phm*/}
            <Modal open={isEditModalOpen} onClose={() => setIsEditModalOpen(false)}>
                <Box sx={modalStyle}>
                    <Typography variant="h5" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.6rem' }}>Sa Sn Phm - ID: {currentEditProduct?._id}</Typography>
                    <TextField label="Tn Sn Phm" name="name" value={currentEditProduct?.name} onChange={e => setCurrentEditProduct({ ...currentEditProduct, name: e.target.value })} fullWidth margin="normal" />
                    <FormControl fullWidth margin="normal">
                        <InputLabel>Loi Sn Phm</InputLabel>
                        <Select
                            name="type"
                            value={productFormData.type}
                            onChange={handleInputChange}
                        >
                            {productTypes.map(type => (
                                <MenuItem key={type._id} value={type._id}>{type.name}</MenuItem>
                            ))}
                        </Select>
                    </FormControl>
                    <TextField label="M t" name="description" value={currentEditProduct?.description} onChange={e => setCurrentEditProduct({ ...currentEditProduct, name: e.target.value })} fullWidth margin="normal" />
                    <TextField
                        label="ImageUrl"
                        name="imageUrl"
                        value={productFormData.imageUrl}
                        onChange={handleInputChange}
                        fullWidth
                        margin="normal"
                        InputProps={{
                            readOnly: true, // Makes the input field read-only
                        }}
                    />
                    <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', marginBottom: 2 }}>
                        {preview && (
                            <Box>
                                <img src={preview} alt="Preview" style={{ maxWidth: '100%', marginBottom: 10 }} />
                            </Box>
                        )}
                        <Button variant="contained" component="label">
                            Select Image
                            <input type="file" hidden onChange={handleFileSelect} />
                        </Button>
                        <Button
                            onClick={uploadImage}
                            variant="contained"
                            color="primary"
                            sx={{ marginTop: 2 }}
                        >
                            Upload Image
                        </Button>
                    </Box>
                    <TextField label="Gi bn" name="buyPrice" value={currentEditProduct?.buyPrice} onChange={e => setCurrentEditProduct({ ...currentEditProduct, name: e.target.value })} fullWidth margin="normal" />
                    <TextField label="Gi nim yt" name="promotionPrice" value={currentEditProduct?.promotionPrice} onChange={e => setCurrentEditProduct({ ...currentEditProduct, name: e.target.value })} fullWidth margin="normal" />
                    <TextField label="S lng" name="amount" value={currentEditProduct?.amount} onChange={e => setCurrentEditProduct({ ...currentEditProduct, name: e.target.value })} fullWidth margin="normal" />
                    <Button onClick={handleUpdateConfirm} style={buttonConfirmStyle}>Cp Nht</Button>
                    <Button onClick={() => setIsEditModalOpen(false)} style={buttonCancelStyle}>Hy</Button>
                </Box>
            </Modal>
            {/* Lc sn phm */}
            <Box
                border={1}
                borderColor="purple"
                borderRadius={4}
                p={2}
                mb={2}
                style={{ backgroundColor: 'lavender', width: '33%' }}
            >
                <Typography variant="h5" color="purple" style={{ ...customFontStyle, margin: '5px 0', fontWeight: 'bold' }}>Lc Sn Phm Theo Tn</Typography>
                <TextField
                    label="Tm kim sn phm theo tn"
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                />
                <br></br>
                <Typography variant="h5" color="purple" style={{ ...customFontStyle, margin: '10px 0', fontWeight: 'bold' }}>Lc Sn Phm Theo Gi</Typography>
                <Slider
                    value={priceRange}
                    onChange={(e, newValue) => setPriceRange(newValue)}
                    valueLabelDisplay="auto"
                    max={100}
                />
                <br></br>
                <Typography variant="h5" color="purple" style={{ ...customFontStyle, margin: '5px 0', fontWeight: 'bold' }}>Lc Sn Phm Theo Loi</Typography>
                <FormGroup>
                    <FormControlLabel
                        control={<Checkbox checked={selectedCategories.includes('Sativa')} onChange={() => handleCategoryChange('Sativa')} />}
                        label="Sativa"
                    />
                    <FormControlLabel
                        control={<Checkbox checked={selectedCategories.includes('Hybrid')} onChange={() => handleCategoryChange('Hybrid')} />}
                        label="Hybrid"
                    />
                    <FormControlLabel
                        control={<Checkbox checked={selectedCategories.includes('Indica')} onChange={() => handleCategoryChange('Indica')} />}
                        label="Indica"
                    />
                </FormGroup>
            </Box>
            {/* Bng hin th danh sch sn phm */}
            <Paper elevation={3} sx={{ margin: 2, textAlign: 'center', padding: 2 }}>
                <Typography variant="h3" component="h2" sx={{ ...customFontStyle, marginBottom: 2 }}>
                    Danh sch sn phm
                </Typography>
                {/* Nt m Modal */}
                <Box sx={{ display: 'flex', justifyContent: 'center', marginBottom: 2 }}>
                    <Button onClick={handleOpenModal} variant="contained" color="success">
                        Thm Sn Phm
                    </Button>
                </Box>
            </Paper>
            <Paper elevation={3} sx={{ margin: 2 }}>
                <Grid container>
                    <Grid item xs={12}>
                        <TableContainer component={Paper} sx={{ overflowX: 'auto' }}>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.3rem' }}>ID</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.3rem' }}>Name</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.3rem' }}>M t sn phm</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.3rem' }}>Loi</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.3rem' }}>Gi nim yt</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.3rem' }}>Gi khuyn mi</TableCell>
                                        <TableCell style={{ fontWeight: 'bold', fontSize: '1.3rem' }}>Actions</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {filteredProducts.map(product => (
                                        <TableRow key={product._id}>
                                            <TableCell style={{ fontSize: '1.1rem' }}>{product._id}</TableCell>
                                            <TableCell style={{ fontSize: '1.1rem' }}>{product.name}</TableCell>
                                            <TableCell style={{ fontSize: '1.1rem' }}>{product.description}</TableCell>
                                            <TableCell style={{ fontSize: '1.1rem' }}>{product.category}</TableCell>
                                            <TableCell style={{ fontSize: '1.1rem' }}>{product.buyPrice}</TableCell>
                                            <TableCell style={{ fontSize: '1.1rem' }}>{product.promotionPrice}</TableCell>
                                            <TableCell>
                                                <Button onClick={() => handleEdit(product)} style={{ backgroundColor: 'green', color: 'white', borderRadius: '5px' }}>Sa</Button>
                                                <Button onClick={() => handleDelete(product._id)} style={{ backgroundColor: 'red', color: 'white', borderRadius: '5px' }}>Xa</Button>
                                            </TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Grid>
                </Grid>
            </Paper>
        </Box>
    );
}

export default ProductTable;



