import { useSelector, useDispatch } from 'react-redux';
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { clearCart, confirmOrder, createCustomer, fetchProvinces, hideModal, showModal } from '../../actions/actions';
import { Button, Modal, TextField, Box, Paper, Typography, FormControl, InputLabel, Select, MenuItem } from '@mui/material';

const modalStyle = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    boxShadow: 24,
    p: 4,
};

const OrderInfo = ({ onCancel }) => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const cart = useSelector(state => state.cart);
    const user = useSelector(state => state.user);
    const isModalVisible = useSelector(state => state.modal.isModalVisible);
    const provinces = useSelector((state) => state.provinces.provinces);
    // nh ngha state cho thng tin khch hng v n hng
    const [customerInfo, setCustomerInfo] = useState({
        fullName: '',
        phone: '',
        email: '',
        address: '',
        city: '',
        district: '',
        country: 'Vietnam'
    });

    const [orderConfirmation, setOrderConfirmation] = useState({ orderId: null, orderDate: null });
    const [selectedCity, setSelectedCity] = useState(null); // Thm state  lu tr thnh ph  chn
    const [districts, setDistricts] = useState([]); // State lu danh sch qun/huyn
    const [selectedDistrict, setSelectedDistrict] = useState('');

    useEffect(() => {
        dispatch(fetchProvinces());

        // c thng tin khch hng t sessionStorage
        const storedCustomerInfo = sessionStorage.getItem('customerInfo');
        if (storedCustomerInfo) {
            const customerData = JSON.parse(storedCustomerInfo);
            setCustomerInfo({
                fullName: customerData.fullName || '',
                phone: customerData.phone || '',
                email: customerData.email || '',
                address: customerData.address || '',
                city: customerData.city || '',
                district: customerData.district || '',
                country: 'Vietnam'
            });
            if (customerData.city) {
                setSelectedCity(customerData.city);
                const selectedProvince = provinces.find(province => province.name === customerData.city);
                if (selectedProvince) {
                    setDistricts(selectedProvince.districts || []);
                    if (customerData.district) {
                        setSelectedDistrict(customerData.district);
                    }
                }
            }
        }
    }, [dispatch, provinces]);


    //  i ngy thng
    const readableOrderDate = orderConfirmation.orderDate ? new Date(orderConfirmation.orderDate).toLocaleDateString() : null;

    //Hm x l nt trn bng
    const handleCancel = () => { if (onCancel) onCancel(); };
    const handleConfirm = () => {
        // Kim tra nu gi hng rng
        if (cart.items.length === 0) {
            alert("Gi hng rng!");
            return; // Thot khi hm nu gi hng khng c sn phm
        }

        // Kim tra xem ngi dng  ng nhp cha trc khi xc nhn n hng
        if (!user.isLoggedIn) {
            alert(`Bn cha ng nhp!`);
            navigate('/login');
            return;
        }

        const orderDetails = {
            products: cart.items.map(item => ({
                product: item.id,
                quantity: item.quantity
            })),
            totalQuantity: cart.items.reduce((total, item) => total + item.quantity, 0),
            total: cart.total
        };
        dispatch(confirmOrder(orderDetails))
            .then(response => {
                setOrderConfirmation({ orderId: response.orderId, orderDate: response.orderDate });
                dispatch(showModal());
            })
            .catch((error) => {
                console.error('Error:', error);
                alert(error.message);
            });
    };
    //Hm x l ng Modal
    const handleCloseModal = () => {
        dispatch(hideModal());
    };
    // Hm x l hy trn Modal
    const handleCancelModal = () => {
        dispatch(hideModal()); // Tt modal
        if (onCancel) onCancel(); // Gi callback onCancel nu c
    };

    //Hm x l trn Modal
    const handleInputChange = (e) => {
        setCustomerInfo({ ...customerInfo, [e.target.name]: e.target.value });
    };


    const handleModalConfirm = () => {
        // Kim tra xem email v phone  c in y  hay khng
        const isComplete = customerInfo.email !== '' && customerInfo.phone !== '';

        if (!isComplete) {
            alert('Xin hy nhp y  Email v S in thoi!');
            return;
        }

        // Include the order information with the customer details
        const customerData = {
            ...customerInfo,
            city: selectedCity, // S dng thnh ph  chn t state
            district: selectedDistrict,
            orders: [{
                orderId: orderConfirmation.orderId,
                orderDate: orderConfirmation.orderDate
            }]
        };
        // Ly token t sessionStorage
        const token = sessionStorage.getItem('token');
        const orderId = orderConfirmation.orderId;
        const userId = sessionStorage.getItem('userId'); // Ly userId t Redux store hoc sessionStorage

        // Kim tra token
        if (!token) {
            alert("Khng c token xc thc!");
            return;
        }

        dispatch(createCustomer(customerData, orderId, userId, token))
            .then(response => {
                alert(response.message);;
                dispatch(clearCart());
                localStorage.removeItem('cart');
                dispatch(hideModal());
            })
            .catch(error => {
                console.error('Error:', error);
                alert(error.response?.data?.message || "C li xy ra!");
            });
    };

    // Font Style
    const customFontStyle = {
        fontFamily: "'Happy Monkey', sans-serif",
    };
    // Item Name Style
    const itemNameStyle = {
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: '1.2rem',
        color: '#333',
    };
    // Item Quantity Style
    const itemQuantityStyle = {
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: '1.1rem',
        color: '#555',
    };
    //Button Confirm Style
    const buttonConfirmStyle = {
        backgroundColor: '#6c8e5d',
        '&:hover': {
            backgroundColor: '#388E3C',
        },
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: 'large',
        color: '#fef7d0',
        fontweight: 'bold',
        cursor: 'pointer',
        margin: '10px 0',
    };
    // Button Cancel Style
    const buttonCancelStyle = {
        backgroundColor: '#fcba03',
        '&:hover': {
            backgroundColor: '#ff0011',
        },
        fontFamily: "'Happy Monkey', sans-serif",
        fontSize: 'large',
        color: '#01723e',
        fontweight: 'bold',
        cursor: 'pointer',
        margin: '20px 0',
    };

    // Hm ny s c gi khi tnh/thnh ph c chn thay i
    const handleCityChange = (event) => {
        const value = event.target.value; // Ly gi tr  chn t s kin onChange
        setSelectedCity(value); // Cp nht gi tr ca selectedCity

        // Tm danh sch qun/huyn da trn tnh/thnh ph c chn
        const selectedProvince = provinces.find(province => province.name === value);

        if (selectedProvince && selectedProvince.districts) {
            setDistricts(selectedProvince.districts); // Cp nht danh sch qun/huyn
            setSelectedDistrict(''); // Reset gi tr qun/huyn  chn
        } else {
            setDistricts([]); // Nu khng tm thy danh sch, t danh sch qun/huyn rng
            setSelectedDistrict(''); // Reset gi tr qun/huyn  chn
        }
    };


    return (
        <div>
            <Box display="flex" justifyContent="flex-end" m={2}>
                <Paper sx={{ padding: 2, backgroundColor: '#fef7d0', boxShadow: 3 }}>
                    <Typography sx={{ ...customFontStyle, fontWeight: 'bold', fontSize: '2rem' }}>Xc nhn n hng</Typography>
                    {cart.items.map(item => (
                        <div key={item.id}>
                            <span style={itemNameStyle}>{item.name}</span>
                            <span style={itemQuantityStyle}>- {item.quantity}</span>
                        </div>
                    ))}
                    <div style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.4rem' }}>Total: {cart.total}</div>
                    <Button variant="outlined" color="primary" onClick={handleConfirm} >Xc nhn</Button>
                    <Button variant="outlined" color="warning" onClick={handleCancel}>Hy</Button>
                    <div>
                        {isModalVisible && (
                            <Modal
                                open={isModalVisible}
                                onClose={handleCloseModal}
                            >
                                <Box sx={modalStyle}>
                                    <h2 style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.6rem' }}>Nhp thng tin giao hng</h2>
                                    <div style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.2rem' }}>Order ID: {orderConfirmation.orderId}</div>
                                    <div style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.2rem' }}>Order Date: {readableOrderDate}</div>
                                    <br />
                                    <TextField fullWidth label="H tn" name="fullName" onChange={handleInputChange} value={customerInfo.fullName} />
                                    <TextField fullWidth label="S in thoi" name="phone" onChange={handleInputChange} value={customerInfo.phone} />
                                    <TextField fullWidth label="Email" name="email" onChange={handleInputChange} value={customerInfo.email} />
                                    <TextField fullWidth label="a ch" name="address" onChange={handleInputChange} value={customerInfo.address} />
                                    <FormControl fullWidth>
                                        <InputLabel>Thnh Ph</InputLabel>
                                        <Select
                                            name="city"
                                            value={selectedCity}
                                            onChange={handleCityChange}
                                        >
                                            {provinces.map((province) => (
                                                <MenuItem key={province.code} value={province.name}>
                                                    {province.name}
                                                </MenuItem>
                                            ))}
                                        </Select>
                                    </FormControl>
                                    <FormControl fullWidth>
                                        <InputLabel>Qun</InputLabel>
                                        <Select
                                            name="district"
                                            value={selectedDistrict}
                                            onChange={(e) => setSelectedDistrict(e.target.value)}
                                        >
                                            {districts.map((district) => (
                                                <MenuItem key={district.code} value={district.name}>
                                                    {district.name}
                                                </MenuItem>
                                            ))}
                                        </Select>
                                    </FormControl>
                                    <FormControl fullWidth>
                                        <Typography>{customerInfo.country}</Typography>
                                        <InputLabel>Quc Gia</InputLabel>
                                    </FormControl>
                                    <br></br>
                                    <br />
                                    <Button onClick={handleModalConfirm} style={buttonConfirmStyle}>Xc nhn mua hng</Button>
                                    <Button onClick={handleCancelModal} style={buttonCancelStyle}>Hy mua hng</Button>
                                </Box>
                            </Modal>
                        )}
                    </div>
                </Paper>
            </Box>
        </div>
    );
};

export default OrderInfo;