import { setUser } from "./userActions";
// action types
//filter Product actions
export const SET_PRODUCT_NAME = 'SET_PRODUCT_NAME';
export const SET_MAX_PRICE = 'SET_MAX_PRICE';
export const SET_MIN_PRICE = 'SET_MIN_PRICE';
export const SET_PRODUCT_TYPE = 'SET_PRODUCT_TYPE';
export const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
export const TOGGLE_PRODUCT_TYPE = 'TOGGLE_PRODUCT_TYPE';
//fetch Product actions
export const FETCH_PRODUCTS_START = 'FETCH_PRODUCTS_START';
export const FETCH_PRODUCTS_SUCCESS = 'FETCH_PRODUCTS_SUCCESS';
export const FETCH_PRODUCTS_FAILURE = 'FETCH_PRODUCTS_FAILURE';
//show modal
export const SHOW_MODAL = 'SHOW_MODAL';
export const HIDE_MODAL = 'HIDE_MODAL';

// Action Types Ly Tnh Thnh
export const FETCH_PROVINCES_REQUEST = 'FETCH_PROVINCES_REQUEST';
export const FETCH_PROVINCES_SUCCESS = 'FETCH_PROVINCES_SUCCESS';
export const FETCH_PROVINCES_FAILURE = 'FETCH_PROVINCES_FAILURE';

// Action Creators  ly tnh thnh
export const fetchProvincesRequest = () => ({
    type: FETCH_PROVINCES_REQUEST,
});

export const fetchProvincesSuccess = (provinces) => ({
    type: FETCH_PROVINCES_SUCCESS,
    payload: provinces,
});

export const fetchProvincesFailure = (error) => ({
    type: FETCH_PROVINCES_FAILURE,
    payload: error,
});

//Fetch Tnh thnh
export const fetchProvinces = () => {
    return (dispatch) => {
        dispatch(fetchProvincesRequest());

        fetch('https://provinces.open-api.vn/api/?depth=2')
            .then((response) => {
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                return response.json();
            })
            .then((provinces) => {
                dispatch(fetchProvincesSuccess(provinces));
            })
            .catch((error) => {
                dispatch(fetchProvincesFailure(error.message));
            });
    };
};

// action creators
export const fetchProducts = () => async (dispatch) => {
    dispatch({ type: FETCH_PRODUCTS_START });

    try {
        const response = await fetch("http://localhost:8000/product");
        if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
        }
        const data = await response.json();
        dispatch({ type: FETCH_PRODUCTS_SUCCESS, payload: data.data });
    } catch (error) {
        dispatch({ type: FETCH_PRODUCTS_FAILURE, payload: error.message });
    }
};

