import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { updateQuantity, updateCart, loginUser } from '../actions/actions';
import {
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    IconButton,
    Paper,
    Box,
    Typography,
    Button
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';
import OrderInfo from './orders/OrderInfo';
import BreadCrumb from './breadcrumbs/BreadCrumb';

const CartPage = () => {
    const dispatch = useDispatch();
    const cart = useSelector(state => state.cart.items);
    const totalPrice = cart.reduce((total, item) => total + item.price * item.quantity, 0).toFixed(2);
    const [showOrderInfo, setShowOrderInfo] = useState(false);
    const isLoggedIn = useSelector(state => state.user.isLoggedIn);


    useEffect(() => {
        if (isLoggedIn === 'true') {
            // Ly token t sessionStorage
            const token = sessionStorage.getItem('token');
            // Dispatch hnh ng ng nhp vi token
            dispatch(loginUser({ token }));
        }
    }, [dispatch]);

    // Cp nht gi hng t localStorage khi component c mount
    useEffect(() => {
        const cartFromStorage = JSON.parse(localStorage.getItem('cart')) || [];
        dispatch(updateCart(cartFromStorage));
    }, [dispatch]);

    // Lng nghe s kin cp nht gi hng t cc phn khc ca ng dng
    useEffect(() => {
        const handleCartUpdate = () => {
            const cartFromStorage = JSON.parse(localStorage.getItem('cart')) || [];
            dispatch(updateCart(cartFromStorage));
        };

        window.addEventListener('cartUpdated', handleCartUpdate);

        return () => {
            window.removeEventListener('cartUpdated', handleCartUpdate);
        };
    }, [dispatch]);

    const handlePurchaseClick = () => {
        setShowOrderInfo(true);
    };

    const handleQuantityChange = (_id, increment) => {
        dispatch(updateQuantity(_id, increment));
    };

    const handleRemove = (id) => {
        const updatedCart = cart.filter(item => item.id !== id);
        dispatch({ type: 'REMOVE_ITEM', payload: updatedCart });
        localStorage.setItem('cart', JSON.stringify(updatedCart));
    };

    const handleCancel = () => {
        setShowOrderInfo(false); // t li trng thi  n OrderInfo
    };

    //BreadCrumb
    const breadcrumbs = [
        {
            name: "Trang ch",
            url: "/"
        },
        {
            name: "Danh sch sn phm",
            url: "/products"
        }
    ];

    // Font Style
    const customFontStyle = {
        fontFamily: "'Happy Monkey', sans-serif",
    };

    return (
        <Box sx={{ padding: 2 }}>
            <BreadCrumb breadcrumbs={breadcrumbs} />
            <Box sx={{ marginBottom: 3 }}>
                <Paper sx={{ padding: 2, backgroundColor: '#fef7d0', boxShadow: 3 }}>
                    <Typography variant="h3" gutterBottom component="div" sx={{ ...customFontStyle, fontWeight: 'bold', textAlign: 'center' }}>
                        Gi Hng
                    </Typography>
                </Paper>
            </Box>
            <TableContainer component={Paper}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.4rem' }}>Sn phm</TableCell>
                            <TableCell align="right" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.4rem' }}>Gi</TableCell>
                            <TableCell align="right" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.4rem' }}>S lng</TableCell>
                            <TableCell align="right" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.4rem' }}>Tng gi</TableCell>
                            <TableCell align="right" style={{ ...customFontStyle, fontWeight: 'bold', fontSize: '1.4rem' }}>Xa</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {cart.map((product) => (
                            <TableRow key={product.id}>
                                <TableCell>{product.name}</TableCell>
                                <TableCell align="right">${product.price.toFixed(2)}</TableCell>
                                <TableCell align="right">
                                    <IconButton onClick={() => handleQuantityChange(product.id, false)} disabled={product.quantity <= 1}>
                                        <RemoveIcon />
                                    </IconButton>
                                    {product.quantity}
                                    <IconButton onClick={() => handleQuantityChange(product.id, true)}>
                                        <AddIcon />
                                    </IconButton>
                                </TableCell>
                                <TableCell align="right">${(product.price * product.quantity).toFixed(2)}</TableCell>
                                <TableCell align="right">
                                    <IconButton onClick={() => handleRemove(product.id)}>
                                        <DeleteIcon />
                                    </IconButton>
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
            <Box display="flex" justifyContent="flex-end" m={2}>
                <Paper sx={{ padding: 2, backgroundColor: '#fef7d0', boxShadow: 3 }}>
                    <Typography variant="h6" gutterBottom component="div" sx={{ ...customFontStyle }}>
                        Tng n Hng: ${totalPrice}
                    </Typography>
                </Paper>
            </Box>
            <Box display="flex" justifyContent="flex-end" m={2}>
                <Button variant="contained" color="primary" onClick={handlePurchaseClick} sx={{ padding: 2, boxShadow: 3 }}>
                    Xc nhn gi hng
                </Button>
            </Box>
            {showOrderInfo && <OrderInfo onCancel={handleCancel} />}
        </Box>
    );
};

export default CartPage;
