import { createStore, combineReducers, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import filterReducer from '../reducers/filterReducer';
import productsReducer from '../reducers/productReducer';
import cartReducer from '../reducers/cartReducer';
import modalReducer from '../reducers/modalReducer';
import userReducer from '../reducers/userReducer';
import provincesReducer from '../reducers/provincesReducer';

// Hm h tr  tnh tng gi ban u t gi hng
function calculateInitialTotal(cartItems) {
    // Kim tra nu cartItems khng tn ti hoc rng
    if (!cartItems || cartItems.length === 0) {
        return 0.00.toFixed(2);
    }
    return cartItems.reduce((total, item) => total + item.price * item.quantity, 0).toFixed(2);
}

const preloadedState = {
    cart: {
        items: JSON.parse(localStorage.getItem('cart')) || [],
        // Bn c th cn tnh li tng gi  y nu n c lu tr
        total: calculateInitialTotal(JSON.parse(localStorage.getItem('cart'))),
    }
};

const rootReducer = combineReducers({
    filter: filterReducer,
    products: productsReducer,
    cart: cartReducer,
    modal: modalReducer,
    user: userReducer,
    provinces: provincesReducer,
});

const store = createStore(
    rootReducer,
    preloadedState,
    applyMiddleware(thunk)
);

export default store;

