import {
    FETCH_PROVINCES_REQUEST,
    FETCH_PROVINCES_SUCCESS,
    FETCH_PROVINCES_FAILURE,
} from '../actions/actions'; // m bo bn import cc action types t file actions ca bn

const initialState = {
    provinces: [],
    loading: false,
    error: null,
};

const provincesReducer = (state = initialState, action) => {
    switch (action.type) {
        case FETCH_PROVINCES_REQUEST:
            return {
                ...state,
                loading: true,
                error: null,
            };
        case FETCH_PROVINCES_SUCCESS:
            return {
                ...state,
                loading: false,
                provinces: action.payload,
            };
        case FETCH_PROVINCES_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.payload,
            };
        default:
            return state;
    }
};

export default provincesReducer;
